* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}
.main_div {
  background-color: #318ce7;
  width: 100%;
  height:100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.center_div {
  background-color: #f4f4f4;
  overflow: hidden;
  width: 25%;
  min-height: 70%;

  border-radius: 1rem;
  box-shadow: 20px 20px 60px rgba(0, 0, 0, 0.404),
    inset 20px 20px 60px rgba(255, 255, 255, 0.363);
}
@media only screen and (max-width: 1200px) {
  .center_div {
    width: 50%;
  }
}
@media only screen and (max-width: 650px) {
  .center_div {
    width: 75%;
  }
}
@media only screen and (max-width: 400px) {
  .center_div {
    width: 95%;
  }
}

.center_div h2 {
  text-align: center;
  font-weight: 500;
  letter-spacing: 3px;
  font-size: 2.4rem;
  text-shadow: 4px 4px 5px rgb(139, 139, 164);
  margin-top: 0.4rem;
}
.center_div input {
  border: 0;
  outline: 0;
  font-size: 1rem;
  min-width: 60%;
  border-bottom: 2px solid black;
  text-decoration: blueviolet;
  font-weight: 400;
  margin-left: 2rem;
  margin-bottom: 2rem;
  text-align: center;
  background-color: #f4f4f4;
  margin-right: 0.5rem;
}

ol li {
  padding-left: 0;
  text-align: left;
  font-size: 20px;
  font-weight: 500;
  min-height: 40px;
  display: flex;
  align-items: center;
  color: #8566aa;
  text-transform:capitalize;

}
  

#todo_style {
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
}
#todo_style button {
  border: none;
  outline: none;

  cursor: pointer;

  margin: 0px 15px 0 35px;
}
